.unit-container{
	margin:10px auto;
}
#root{
	background: -webkit-linear-gradient(150deg,#a5a5a5 , #636);
	background: -o-linear-gradient(150deg,#a5a5a5 , #636);
	background: linear-gradient(-60deg,#a5a5a5 , #636);
}
.ticket {
  width: 320px;
  margin: 0 auto;
  position: relative;
  -webkit-filter: drop-shadow(1px 1px 3px rgba(0, 0, 0, 0.3));
          filter: drop-shadow(1px 1px 3px rgba(0, 0, 0, 0.3));
}
.ticket .top, .ticket .bottom, .ticket .rip {
  background-color: #fff;
}
.ticket .top {
  border-top-right-radius: 5px;
  border-top-left-radius: 5px;
}
.deetz {
  	padding:5px 18px 10px 18px;
  	color: 	#000;
}
.ticket-head{
	padding:10px 18px 5px 18px;
	color: 	#636;
}
.ticket .bottom {
  border-bottom-right-radius: 5px;
  border-bottom-left-radius: 5px;
  padding: 18px;
  height: 60px;
  padding-top: 10px;
}
.barcode {
  background-image: url(/Images/dots.png);
  background-repeat: repeat-y;
  min-width: 58px;
}
.buy {
  display: block;
  font-size: 12px;
  font-weight: bold;
  background-color: #636;
  padding: 0 18px;
  line-height: 30px;
  border-radius: 15px;
  color: #fff;
  text-decoration: none;
}
.buy:hover{
	background: #000;
}
.unit-container div.ticket .rip {
  height: 20px;
  margin: 0 10px;
  background-image: url(/Images/barcode.png);
  background-size: 4px 2px;
  background-repeat: repeat-x;
  background-position: center;
  position: relative;
  -webkit-box-shadow: 0 1px 0 0 #ffffff, 0 -1px 0 0 #ffffff;
          box-shadow: 0 1px 0 0 #ffffff, 0 -1px 0 0 #ffffff;
}
.unit-container div.ticket .rip:before,
.unit-container div.ticket .rip:after {
  content: '';
  position: absolute;
  width: 20px;
  height: 20px;
  top: 50%;
  -webkit-transform: translate(-50%, -50%) rotate(45deg);
      -ms-transform: translate(-50%, -50%) rotate(45deg);
          transform: translate(-50%, -50%) rotate(45deg);
  border-radius: 50%;
  -webkit-box-shadow: 10px -10px 0px 7px white;
          box-shadow: 10px -10px 0px 7px white;
}
.unit-container div.ticket .rip:before {
  left: -10px;
}
.unit-container div.ticket .rip:after {
  -webkit-transform: translate(-50%, -50%) rotate(225deg);
      -ms-transform: translate(-50%, -50%) rotate(225deg);
          transform: translate(-50%, -50%) rotate(225deg);
  right: -30px;
}
